
export class GetTime {
  date = new Date();
  newDate() {
    return new Date(this.date);
  }
  get getYear() {
    let newDate = this.newDate();
    return newDate.getFullYear();
  }
  get getMonth() {
    let newDate = this.newDate();
    return newDate.getMonth();
  }
  get getDate() {
    let newDate = this.newDate();
    return newDate.getDate();
  }
}

class Picker {
  constructor(firstDate, weekOfFirstDate){
    this.firstDate = firstDate;
    this.weekOfFirstDate = weekOfFirstDate;
    this.oneArr = this.generateArray(this.firstDate, 7);
  }
  generateArray(num, len) {
    return Array.from(new Array(len), (v, i) => {return i + num});
  }
  get colCalendarArray() {
    if( !this.weekOfFirstDate ) return this.oneArr;
    let nextDateNum = this.weekOfFirstDate,
      nextDateArr = this.generateArray(1, 7 - nextDateNum);
    this.oneArr.splice(nextDateNum, 7, ...nextDateArr);
    return this.oneArr;
  }
}

export default Picker;



